body {
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: monospace;
}

.search-bar {
    height: 100px;
    width: 100%;
}

.search-bar-container {
    width: 100%;
    border: 0;
    padding: 0;
    display: -webkit-flex;
    display: flex;
    overflow: hidden;
    position: relative;
    max-height: 1000px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    background-size: cover;
    background-position: center center;
    margin: auto 0 80px;
}

.search {
    position: relative;
    border-radius: 8px;
    background-color: rgba(114, 66, 117, 0.5);
    margin-right: 16px;
    width: 70%;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}

.search:hover, .search:focus-within {
    background-color: rgba(114, 66, 117, 0.7);
}

.searchIcon {
    width: 36px;
    height: 100%;
    position: absolute;
    pointer-events: none;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}

.whiteSearch {
    color: white;
}

.inputRoot {
    color: inherit;
    width: calc(100% - 36px - 150px);
    padding-left: 36px;
    height: 100%;
}

.inputInput {
    padding-top: 8px;
    padding-right: 8px;
    padding-bottom: 8px;
    padding-left: 80px;
    width: 100%;
}

.inputDate, .inputDate-left {
    width: 150px;
    height: 48px;
}

.inputDate-left {
    margin-right: 8px;
}
.image-center-cropped {
    object-fit: cover; /* Do not scale the image */
    padding-top: 100%;
    width: 100%;
}
.split-content-box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}

.split-content-left {
    margin-right: 20px;
    max-width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.right-aligned-date {
    margin-top: 8px;
    margin-left: auto;
}

.split-content-right {
    max-width: 50%;
}

.image {
    object-fit: contain;
    max-width:100%;
    max-height:100%;
}


.App-content {
    position: relative;
    z-index: 1000;
}

.App-loading-container {
    display: -webkit-flex;
    display: flex;
    width: 100%;
    -webkit-justify-content: center;
            justify-content: center;
}

